.slick-next,
.slick-prev {
  font-size: 0;
  line-height: 0;
  top: 50%;
  width: 20px;
  height: 20px;
  -webkit-transform: translate(0, -50%);
  -ms-transform: translate(0, -50%);
  transform: translate(0, -100%);
  cursor: pointer;
  color: transparent;
  border: none;
  outline: 0;
  background: 0 0;
}
.slick-next:focus,
.slick-next:hover,
.slick-prev:focus,
.slick-prev:hover {
  color: transparent;
  outline: 0;
  background: 0 0;
}
.slick-next:focus:before,
.slick-next:hover:before,
.slick-prev:focus:before,
.slick-prev:hover:before {
  opacity: 1;
}
.slick-next.slick-disabled:before,
.slick-prev.slick-disabled:before {
  opacity: 0;
  cursor: default;
}
.slick-next:before,
.slick-prev:before {
  font-size: 1px;
  line-height: 0;
  font-family: FontAwesome;
  color: #363636;
  font-weight: 100;
}
.slick-prev {
  left: -3.7%;
}
[dir="rtl"] .slick-prev {
  right: 0;
}
.slick-prev:before {
  content: url("../../assets/angle-left.png");
}
.slick-next:before,
[dir="rtl"] .slick-prev:before {
  content: url("../../assets/angle-right.png");
}
.slick-next {
  right: -2.7%;
}
[dir="rtl"] .slick-next {
  right: auto;
  left: -25px;
}

.slick-next:hover:before {
  content: url("../../assets/angle-right-active.png");
}

.slick-prev:hover:before {
  content: url("../../assets/angle-left-active.png");
}

.slick-dotted.slick-slider {
  margin-bottom: 30px;
}
.slick-dots {
  bottom: -35px;
  width: 100%;
  margin: 0;

  list-style: none;
  text-align: center;
}
.slick-dots li {
  position: relative;
  display: inline-block;
  width: 20px;
  height: 25px;
  margin: -2px;
  cursor: pointer;
}
.slick-dots li button {
  font-size: 0;
  line-height: 0;
  display: block;
  width: 20px;
  height: 20px;
  padding: 5px;
  cursor: pointer;
  color: transparent;
  border: 0;
  outline: 0;
  background: 0 0;
}
.slick-dots li button:focus,
.slick-dots li button:hover {
  outline: 0;
}
.slick-dots li button:focus:before,
.slick-dots li button:hover:before {
  opacity: 1;
  color: #3f3fff;
}
